import { connect } from "react-redux"
import { hasSubmitSucceeded } from "redux-form"
import ContactForm from "../components/contactForm/ContactForm"
import { contactUsWithProjectInformation, setPage } from "../actions/forms"

function mapStateToProps(state) {
  return {
    submitSucceeded: hasSubmitSucceeded("contactUs")(state),
  }
}

function mapDispatchToProps(dispatch) {
  return {
    onSubmit: data => dispatch(contactUsWithProjectInformation(data)),
    setPage: location => dispatch(setPage(location)),
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ContactForm)
