import React, { Component } from "react"

class RenderField extends Component {
  render() {
    const {
      input,
      placeholder,
      type,
      id,
      disabled,
      className,
      required,
    } = this.props

    let inputEl
    switch (type) {
      case "textarea":
        inputEl = (
          <textarea
            {...input}
            id={id || input.name}
            placeholder={placeholder}
            className={`${className} ${disabled ? "disabled" : ""}`}
            disabled={disabled}
            required={required}
          />
        )
        break
      case "select":
        inputEl = (
          <select
            {...input}
            id={id || input.name}
            placeholder={placeholder}
            className={`${className} ${disabled ? "disabled" : ""}`}
            disabled={disabled}
            required={required}
          >
            {this.props.children}
          </select>
        )
        break
      default:
        inputEl = (
          <input
            {...input}
            id={id || input.name}
            type={type}
            placeholder={placeholder}
            className={`${className} ${disabled ? "disabled" : ""}`}
            disabled={disabled}
            required={required}
          />
        )
    }

    return <div className="field-wrapper">{inputEl}</div>
  }
}

export default RenderField
