import React from "react"
import { Field, reduxForm } from "redux-form"
import { Location } from "@reach/router"
import posed from "react-pose"
import RenderField from "../../forms/renderField"
import styles from "./contactForm.module.sass"
import SubscribeFormContainer from "../../containers/SubscribeFormContainer"
import chatImage from "../../images/icon-send-message.svg"

const ThankYouMessage = posed.div({
  enter: { opacity: 1 },
  exit: { opacity: 0 },
})

class ContactForm extends React.Component {
  componentDidMount() {
    this.props.setPage(this.props.location)
  }

  render() {
    const { handleSubmit, isSubscribe, submitSucceeded } = this.props
    return (
      <div className={styles.contactFormWrapper}>
        <form onSubmit={handleSubmit}>
          <h2 className="light">Contact Us</h2>
          <div className={styles.inner}>
            {submitSucceeded ? (
              <ThankYouMessage
                pose={submitSucceeded ? "enter" : "exit"}
                initialPose="exit"
              >
                <h4 className="light">Thanks, we will get back to you soon!</h4>
              </ThankYouMessage>
            ) : (
              <>
                <div className={styles.twoColumns}>
                  <div>
                    <label htmlFor="firstName">First Name</label>
                    <Field
                      component={RenderField}
                      disabled={submitSucceeded}
                      required={true}
                      name="firstName"
                      type="text"
                      placeholder="Jonathan"
                    />
                  </div>
                  <div>
                    <label htmlFor="lastName">Last Name</label>
                    <Field
                      component={RenderField}
                      disabled={submitSucceeded}
                      required={true}
                      name="lastName"
                      type="text"
                      placeholder="Snow"
                    />
                  </div>
                </div>
                <div>
                  <label htmlFor="email">Email</label>
                  <Field
                    component={RenderField}
                    disabled={submitSucceeded}
                    required={true}
                    name="email"
                    type="email"
                    placeholder="jonathan@thewall.com"
                  />
                </div>
                <div>
                  <label htmlFor="email">Your Project</label>
                  <Field
                    component={RenderField}
                    disabled={submitSucceeded}
                    required={true}
                    name="yourProject"
                    type="textarea"
                    placeholder="Tell us about your project..."
                  />
                </div>
                <div className={styles.twoColumns}>
                  <div className={styles.contactCTASection}>
                    <h5 className={styles.bold}>
                      Make sure the email is correct so we can reach you.
                    </h5>
                    <h5>Usually we respond within 24 hours.</h5>
                  </div>
                  <div className={styles.contactCTASection}>
                    <button type="submit" className={styles.sendMessage}>
                      <img src={chatImage} alt="" />
                      <span>Send Message</span>
                    </button>
                  </div>
                </div>
              </>
            )}
          </div>
        </form>
        {isSubscribe && <SubscribeFormContainer />}
      </div>
    )
  }
}

ContactForm = reduxForm({
  form: "contactUs",
})(ContactForm)

export default props => (
  <Location>
    {locationProps => <ContactForm {...locationProps} {...props} />}
  </Location>
)
