import React from "react"
import { Field, reduxForm } from "redux-form"
import RenderField from "../../forms/renderField"
import styles from "./subscribeForm.module.sass"
import contactFormStyles from "../contactForm/contactForm.module.sass"
import mailImage from "../../images/icon-subscribe.svg"
import gitHubImage from "../../images/icon-github-discussions-light.svg"
import discordImage from "../../images/icon-discord-white.svg"

class SubscribeForm extends React.Component {
  render() {
    const { handleSubmit, submitSucceeded } = this.props
    return (
      <form onSubmit={handleSubmit}>
        <div className={styles.subscribeFormWrapper}>
          <div className={styles.subscribeInner}>
            <div className={styles.left}>
              {submitSucceeded ? (
                <div className={styles.subscribeInputSubmitted}>
                  <h2>Thank you for subscribing.</h2>
                  <button className="button success large">Subscribed</button>
                </div>
              ) : (
                <>
                  <h2>Stay in touch for important updates.</h2>

                  <div className={styles.subscribeInput}>
                    <div>
                      <label htmlFor="email">Email</label>
                      <Field
                        name="email"
                        id="subscribeFormEmail"
                        component={RenderField}
                        type="email"
                        placeholder="jonathan@thewall.com"
                        disabled={submitSucceeded}
                        required={true}
                      />
                    </div>
                    <button
                      type="submit"
                      className={contactFormStyles.sendMessage}
                    >
                      <img src={mailImage} alt="" />
                      Subscribe
                    </button>
                  </div>
                </>
              )}
            </div>

            <div className={styles.right}>
              <h2>Join the community.</h2>
              <div className={styles.ctaSection}>
                <a
                  href="https://github.com/kintohub/kintohub-docs/issues"
                  className={styles.engagementLink}
                >
                  <img src={gitHubImage} alt="" />
                  Github Backlog
                </a>
                <a
                  href="https://discordapp.com/invite/QVgqWuw"
                  className={styles.engagementLink}
                >
                  <img src={discordImage} alt="" />
                  Chat on Discord
                </a>
              </div>
            </div>
          </div>
        </div>
      </form>
    )
  }
}

SubscribeForm = reduxForm({
  form: "subscribe",
})(SubscribeForm)

export default SubscribeForm
