  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll)
    this.setState({ pageLoaded: true })
    window.addEventListener("click", this.onClickOutside, false)
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll)
    window.removeEventListener("click", this.onClickOutside, false)
  }

  handleScroll = e => {
    // because chrome does not recognise pageY
    const scrollDistance = e.pageY || e.target.scrollingElement.scrollTop
    const needSolidScrollBar = scrollDistance >= 60
    if (needSolidScrollBar) {
      this.setState({ isScrolled: true })
    } else {
      this.setState({ isScrolled: false })
    }
  }

  onClickOutside = e => {
    if (this.state.showFeatures === true && e.target.id !== "featuresButton") {
      if (
        e.target.parentNode.id !== "featuresDropdown" ||
        e.target.offsetParent.id !== "featuresDropdown"
      ) {
        this.setState({ showFeatures: false })
      }
    }
  }

  toggle = () => this.setState({ isOpen: !this.state.isOpen })
  openFeatures = () => this.setState({ showFeatures: !this.state.showFeatures })

